<template>
  <div class="content-page">
    <div class="search-box">
      <div class="real-box clearfix">
        <div>
          <el-input v-model="kw" placeholder="请输入标题关键字" class="big-input left" style="width: 624px"
                    @change="kwChange" clearable></el-input>
          <button class="search-btn" @click="searchClick"><i class="iconfont icon-sousuo"></i> 搜索</button>
        </div>
        <p><span class="img"></span><span>热门搜索 : </span><span class="item" v-for="(v,i) in tjList" :key="i"
                                                              :class="{'act':rmIndex===i}"
                                                              @click="hotClick(v,i)">{{v.keyWorldName}}</span></p>
      </div>
    </div>
    <div class="base-content">
      <el-scrollbar style="height: 100%" v-if="!showSearch">
        <div class="flex-box">
          <div class="item" v-for="(v,i) in baseInfoList" :key="i">
            <div class="title-box" :class="v.type" @click="$router.push({path:v.url,query:{id:v.fileType}})">
              <span class="img"></span>
              <span class="real-title">{{v.title}}</span>
            </div>
            <ul class="content">
              <li v-for="(x,y) in v.list" :key="y">
                <span :title="x.fileName"><span class="dian"></span>{{x.fileName}}</span>
                <span class="right">{{x.filePublishTime.split(' ')[0]}}</span>
              </li>
              <p v-if="v.list.length == 0" class="np">暂无数据</p>
            </ul>
          </div>
        </div>
      </el-scrollbar>
      <div class="search-content-box" v-if="showSearch">
        <div class="type-ul-box">
          <ul>
            <li v-for="(v,i) in searchList" :key="i" :class="{'act':searchSelected===i}"
                @click="searchTypeClick(v,i)"><span>{{v.name}}({{v.num}})</span></li>
          </ul>
        </div>
        <el-scrollbar class="scroll-bg" v-loading="loading"
               element-loading-text="加载中"
               element-loading-spinner="loading-type-a"
               element-loading-background="rgba(255, 255, 255, 1)" height="100%">
          <ul class="type-content-box">
            <li v-for="(v,i) in searchInfoList" :key="i">
              <div class="title">{{v.fileName}}</div>
              <div class="content">{{v.fileDescribe}}</div>
              <span class="type"><span class="t">来自 : </span>{{setFileType(v.knowledgeType)}}</span>
              <span class="time"><span class="t">发布时间 : </span>{{v.filePublishTime}}</span>
              <span class="look"><span class="t">浏览 : </span>{{v.fileNum}} <i class="iconfont icon-xianshimima"></i></span>
            </li>
          </ul>
        </el-scrollbar>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "thinkTank",
    data() {
      return {
        showSearch:false,
        kw: '',
        tjList: [],
        rmIndex: null,
        baseInfoList: [
          {
            title: '法律法规',
            type: 'sj',
            url:'/knowledgeHouse/detailPage',
            fileType:"1",
            list: []
          }, {
            title: '标注规范',
            type: 'ts',
            url:'/knowledgeHouse/detailPage',
            fileType:"2",
            list: []
          }, {
            title: '公司红头文件',
            type: 'qz',
            url:'/knowledgeHouse/detailPage',
            fileType:"3",
            list: []
          }, {
            title: '产品资料库',
            type: 'fa',
            url:'/knowledgeHouse/detailPage',
            fileType:"4",
            list: []
          }, {
            title: '技术资料库',
            type: 'sz',
            url:'/knowledgeHouse/detailPage',
            fileType:"5",
            list: []
          }, {
            title: '我的智囊',
            type: 'wd',
            url:'/knowledgeHouse/detailPage',
            fileType:"6",
            list: []
          },
        ],
        searchList: [
          {name: '全部', num: 0,type:''},
          {name: '法律法规', num: 0,type:'1'},
          {name: '标注规范', num: 0,type:'2'},
          {name: '公司红头文件', num: 0,type:'3'},
          {name: '产品资料库', num: 0,type:'4'},
          {name: '技术资料库', num: 0,type:'5'},
          {name: '我的智囊', num: 0,type:'6'},
        ],
        searchType:'',//类型搜索
        searchSelected: 0,
        searchInfoList: [],
        loading:true,
      }
    },
    methods: {
      searchTypeClick(v,n) {
        this.searchType=v.type;
        this.searchSelected = n;
        this.searchKwFile();
      },
      kwChange(val) {
        let n = 0;
        for (let i of this.tjList) {
          if (val === i) {
            n++;
            break;
          }
        }
        if (!n) {
          this.rmIndex = null;
        }
      },
      //热词点击
      hotClick(v,i){
            this.showSearch=true;
            this.kw=v.keyWorldName;
            this.rmIndex=i;
            this.searchKwFile();
      },
      setFileType(v){
         let str ="";
         for(let i=0;i<this.baseInfoList.length;i++){
            if(v == this.baseInfoList[i].fileType){
                 str=this.baseInfoList[i].title;
            }
         };
         return str;
      },
      //获取知识库文件
      getKwFile(){
        this.$http.get("StatisticsAndAlarm/knowledegBase/query").then(res=>{
             if(res.data.code == 0){
                let fileArr = res.data.content.dataList;
               
                for(let i =0;i<this.baseInfoList.length;i++){
                   for(let i =0;i<fileArr.length;i++){
                       if(this.baseInfoList[i].fileType == fileArr[i].type){
                          this.baseInfoList[i].list=fileArr[i].folder;
                       }
                   }
                };
                
             }
        })
      },
      //点击搜索
      searchClick(){
          this.showSearch=true;
          this.searchKwFile()
      },
      //搜索知识库文件
      searchKwFile(){
      
        this.searchList=[
          {name: '全部', num: 0,type:''},
          {name: '法律法规', num: 0,type:'1'},
          {name: '标注规范', num: 0,type:'2'},
          {name: '公司红头文件', num: 0,type:'3'},
          {name: '产品资料库', num: 0,type:'4'},
          {name: '技术资料库', num: 0,type:'5'},
          {name: '我的智囊', num: 0,type:'6'},
        ],
        this.loading=true;
        this.$http.get("StatisticsAndAlarm/knowledegBase/query",{
          params:{
              knowledgeType:this.searchType,
              keyWorld:this.kw
          }
        }).then(res=>{
             if(res.data.code == 0){
                this.loading=false;
                let fileArr = res.data.content.dataList;
                let file=[];
                for(let i =0;i<fileArr.length;i++){
                   for(let j =0;j<fileArr[i].folder.length;j++){
                      file.push(fileArr[i].folder[j])
                   }
                };
                for(let i=0;i<this.searchList.length;i++){
                      for(let j =0;j<fileArr.length;j++){
                         if(this.searchList[i].type == fileArr[j].type){
                            this.searchList[i].num=fileArr[j].size.size;
                         }
                      }
                      // if(this.searchList[i].type == ''){
                      //    this.searchList[i].num = list[list.length-1].size;
                      // }
                }
                this.searchList[0].num=res.data.content.info.sum;
                this.searchInfoList=file;
             }
        })
      },
      //获取热词
      getHotSearch(){
         this.$http.get("StatisticsAndAlarm/keyWorlds/query").then(res=>{
                 if(res.data.code == 0){
                   this.tjList=res.data.content.dataList;
                 }
         })
      },
      //获取各类文件数量
      getFileNum(){
         this.$http.get("StatisticsAndAlarm/knowledegBase/queryNumbers").then(res=>{
               if(res.data.code == 0){
                  let list = res.data.content.dataList;
                  for(let i=0;i<this.searchList.length;i++){
                      for(let j =0;j<list.length;j++){
                         if(this.searchList[i].type == list[j].type){
                            this.searchList[i].num=list[j].count
                         }
                      }
                      if(this.searchList[i].type == ''){
                         this.searchList[i].num = list[list.length-1].size;
                      }
                  }
               }
         })
      },
      //页面初始化获取数据
      pageInit(){
        this.getKwFile();
        this.searchKwFile();
        this.getHotSearch();
       // this.getFileNum()
      },
    },
    created() {

      this.pageInit()
    },
    mounted() {

    },
    beforeDestroy() {

    },
    computed: {},
    watch: {},
    components: {}
  }
</script>

<style scoped lang="less">
.np{
  text-align: center;
  height: 30px;
  line-height: 30px;
  color: #666;
}
  .content-page {
    padding-bottom: 12px;
    height: 100%;
    .search-box {
      text-align: center;
      height: 90px;
      padding-top: 23px;
      background: url("../../assets/images/thinkTank/search-bg.png") no-repeat;
      background-size: 100% 100%;
      .real-box {
        display: inline-block;
        height: 100%;
        > div {
          height: 40px;
        }
        .search-btn {
          background: #166BCE;
          color: #fff;
          height: 40px;
          width: 100px;
          float: left;
          border: none;
          outline: none;
          cursor: pointer;
        }
        p {
          text-align: left;
          height: 27px;
          line-height: 27px;
          color: #666666;
          span {
            display: inline-block;
            height: 27px;
            vertical-align: top;
          }
          .img {
            width: 27px;
            background: url("../../assets/images/thinkTank/huo.png") center center no-repeat;
          }
          .item {
            margin: 0 10px;
            cursor: pointer;
            &:hover {
              color: #1a1a1a;
              font-weight: bold;
            }
            &.act {
              color: #166BCE;
            }
          }
        }
      }
    }
    .base-content {
      height: calc(100% - 90px);
      margin-top: 12px;
     // padding: 0 75px;
      .flex-box {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        /*height: 100%;*/
        margin: 0 12px;
        .item {
          width: calc(33.3% - 8px);
          border: 1px solid #C6D1DE;
          padding: 12px 12px 0 12px;
          height: 302px;
          &:nth-child(1), &:nth-child(2), &:nth-child(3) {
            margin-bottom: 12px;
          }
          .title-box {
            height: 108px;
            line-height: 108px;
            text-align: center;
            cursor: pointer;
            &.sj {
              background: url("../../assets/images/thinkTank/sj-bg.png") no-repeat;
              background-size: 100% 100%;
              .img {
                background: url("../../assets/images/thinkTank/sj.png") center center no-repeat;
              }
            }
            &.ts {
              background: url("../../assets/images/thinkTank/ts-bg.png") no-repeat;
              background-size: 100% 100%;
              .img {
                background: url("../../assets/images/thinkTank/ts.png") center center no-repeat;
              }
            }
            &.qz {
              background: url("../../assets/images/thinkTank/qz-bg.png") no-repeat;
              background-size: 100% 100%;
              .img {
                background: url("../../assets/images/thinkTank/qz.png") center center no-repeat;
              }
            }
            &.fa {
              background: url("../../assets/images/thinkTank/fa-bg.png") no-repeat;
              background-size: 100% 100%;
              .img {
                background: url("../../assets/images/thinkTank/fa.png") center center no-repeat;
              }
            }
            &.sz {
              background: url("../../assets/images/thinkTank/sz-bg.png") no-repeat;
              background-size: 100% 100%;
              .img {
                background: url("../../assets/images/thinkTank/sz.png") center center no-repeat;
              }
            }
            &.wd {
              background: url("../../assets/images/thinkTank/wd-bg.png") no-repeat;
              background-size: 100% 100%;
              .img {
                background: url("../../assets/images/thinkTank/wd.png") center center no-repeat;
              }
            }
            span {
              vertical-align: middle;
              display: inline-block;
            }
            .img {
              width: 50px;
              height: 50px;
            }
            .real-title {
              width: 155px;
              margin-left: 28px;
              text-align: center;
              font-size: 18px;
              font-weight: bold;
              color: #fff;
              background: url("../../assets/images/thinkTank/line.png") center center no-repeat;
              background-size: 100%;
            }
          }
          .content {
            li {
              height: 36px;
              line-height: 36px;
              cursor: pointer;
              &:hover {
                background: #EFF3F5;
              }
              > span {
                display: inline-block;
                height: 100%;
              }
              > span:first-child {
                width: calc(100% - 80px);
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
              }
              .right {
                width: 80px;
              }
            }
            li + li {
              border-top: 1px dashed #C6D1DE;
            }
          }
        }
      }
      .search-content-box {
        height: 100%;
        .type-ul-box {
          height: 36px;
          border-bottom: 1px solid #C6D1DE;
          text-align: center;
          ul {
            display: inline-block;
            width: 724px;
            height: 100%;
            li {
              float: left;
              height: 100%;
              line-height: 35px;
              width: calc(100% / 7);
              span {
                display: inline-block;
                height: 100%;
                cursor: pointer;
                position: relative;
              }
              &.act {
                color: #166BCE;
                font-weight: bold;
                span:after {
                  content: '';
                  display: inline-block;
                  position: absolute;
                  left: 0;
                  bottom: -1px;
                  width: 100%;
                  height: 2px;
                  background: #166BCE;
                }
              }
            }
          }
        }
        .scroll-bg {
          height: calc(100% - 36px);
          text-align: center;
          background: url('../../assets/images/thinkTank/content-bg.png') no-repeat;
          background-size: 100% 100%;
          padding-bottom: 12px;
        }
        .type-content-box {
          display: inline-block;
          width: 724px;
          text-align: left;
          cursor: pointer;
          li {
            &:hover {
              background: #EFF3F5;
              .title {
                color: #166BCE;
              }
            }
            padding: 12px 0;
            .title {
              height: 40px;
              line-height: 40px;
              font-weight: bold;
              font-size: 15px;
            }
            .content {
              height: 17px;
              line-height: 17px;
              width: 100%;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
            > span {
              display: inline-block;
              height: 40px;
              line-height: 40px;
              &.type {
                width: 160px;
              }
              &.time {
                width: 280px;
              }
            }
            .t {
              color: #666666;
            }
          }
          li + li {
            border-top: 1px dashed #C6D1DE;
          }
        }
      }
    }
  }
</style>



